import React from "react";
import { Dropdown } from "react-bootstrap";
import { Check2Square } from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
const AcademicsDash = () => {
	const navigate = useNavigate();
	return (
		<>
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`exams`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<Check2Square className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">Scores</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
		</>
	);
};

export default AcademicsDash;
