import React, { useEffect } from "react";
import AcademicsDash from "../Academics/AcademicsDash";
import { useDispatch, useSelector } from "react-redux";
import AdminDash from "../Admin/AdminDash";
import FinanceDash from "../Finance/FinanceDash";
import LibraryDash from "../Library/LibraryDash";
import { People, ClockHistory, Bell } from "react-bootstrap-icons";
import { useNavigate } from "react-router-dom";
import ClientDash from "../Client/ClientDash";
import StoreDash from "../Store/StoreDash";
import { FetchLiveSubs } from "../../Network/SubApi";
import { VerifySub } from "../../Utils/VerifySub";
import { Dropdown } from "react-bootstrap";

const HomeDash = () => {
	//redux dispatch
	const dispatch = useDispatch();
	const navigate = useNavigate();

	useEffect(() => {
		FetchLiveSubs(User.token, ActiveCred.instLinker, 0, dispatch);
	}, []);

	const User = useSelector((state) => state.auth.user);
	const ActiveCred = useSelector((state) => state.cred.activeCred);
	const inst = useSelector((state) => state.inst.activeInst);
	const subs = useSelector((state) => state.sub.subs);
	const Sub = VerifySub(subs, ActiveCred.instLinker);

	return Sub > 0 ? (
		<div className="row m-3 d-flex justify-content-around">
			<div className="col-6 col-md-4 ">
				{parseInt(ActiveCred.admin) === 1 && inst.sub === "Kenya" ? (
					<div className="dashboard mb-2">
						<Dropdown className="no-caret" style={{ width: "100%" }}>
							<Dropdown.Toggle
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}
								variant="transparent"
							>
								<div>
									<div className="d-flex justify-content-center">
										<Bell className="dash-icon" />
									</div>
									<p className="text-center dash-text text-wrap">
										Notifications
									</p>
								</div>
							</Dropdown.Toggle>
							<Dropdown.Menu>
								<Dropdown.Item>
									<p
										className="btn-link"
										onClick={() => {
											window.scroll(0, 0);
											navigate(`notifications`);
										}}
									>
										General Notifications
									</p>
								</Dropdown.Item>

								<Dropdown.Item>
									<p
										className="btn-link"
										onClick={() => {
											window.scroll(0, 0);
											navigate(`bulk-sms`);
										}}
									>
										SMS Notifications
									</p>
								</Dropdown.Item>
							</Dropdown.Menu>
						</Dropdown>
					</div>
				) : (
					<div className="dashboard mb-2">
						<Dropdown
							className="no-caret"
							style={{ width: "100%" }}
							onClick={() => {
								window.scroll(0, 0);
								navigate(`notifications`);
							}}
						>
							<Dropdown.Toggle
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}
								variant="transparent"
							>
								<div>
									<div className="d-flex justify-content-center">
										<Bell className="dash-icon" />
									</div>
									<p className="text-center dash-text text-wrap">
										Notifications
									</p>
								</div>
							</Dropdown.Toggle>
						</Dropdown>
					</div>
				)}
			</div>
			{parseInt(ActiveCred.admin) === 1 ? <AdminDash></AdminDash> : null}
			{parseInt(ActiveCred.admin) === 1 ||
			parseInt(ActiveCred.finance) === 1 ? (
				<FinanceDash></FinanceDash>
			) : null}
			{parseInt(ActiveCred.admin) === 1 ||
			parseInt(ActiveCred.finance) === 1 ||
			parseInt(ActiveCred.library) === 1 ||
			parseInt(ActiveCred.tutor) === 1 ? (
				<>
					<div className="col-6 col-md-4 ">
						<div className="dashboard mb-2">
							<Dropdown
								className="no-caret"
								style={{ width: "100%" }}
								onClick={() => {
									window.scroll(0, 0);
									navigate(`students`);
								}}
							>
								<Dropdown.Toggle
									style={{
										width: "100%",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
									}}
									variant="transparent"
								>
									<div>
										<div className="d-flex justify-content-center">
											<People className="dash-icon" />
										</div>
										<p className="text-center dash-text text-wrap">Students</p>
									</div>
								</Dropdown.Toggle>
							</Dropdown>
						</div>
					</div>
					<AcademicsDash></AcademicsDash>
					<LibraryDash></LibraryDash>
					<StoreDash></StoreDash>
				</>
			) : null}
			{parseInt(ActiveCred.student) === 1 ||
			parseInt(ActiveCred.guardian) === 1 ? (
				<ClientDash></ClientDash>
			) : null}
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`timetable`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<ClockHistory className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">TimeTable</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
		</div>
	) : (
		<></>
	);
};

export default HomeDash;
