import React from "react";
import { Book, CashCoin, Mortarboard } from "react-bootstrap-icons";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
const ClientDash = () => {
	const Navigate = useNavigate();
	const ActiveCred = useSelector((state) => state.cred.activeCred);
	return (
		<>
			<div className="col-6 col-md-4 ">
				<div
					className="dashboard"
					onClick={() => {
						Navigate(
							`students/student/academics/${
								parseInt(ActiveCred.guardian) === 1
									? ActiveCred.refLinker
									: ActiveCred.linker
							}`
						);
					}}
				>
					<div className="d-flex justify-content-center">
						<Mortarboard className="dash-icon" />
					</div>
					<p className="text-center dash-text text-wrap">Academics</p>
				</div>
			</div>
			<div className="col-6 col-md-4 ">
				{" "}
				<div
					className="dashboard"
					onClick={() => {
						Navigate(
							`students/student/finance/${
								parseInt(ActiveCred.guardian) === 1
									? ActiveCred.refLinker
									: ActiveCred.linker
							}`
						);
					}}
				>
					<div className="d-flex justify-content-center">
						<CashCoin className="dash-icon" />
					</div>
					<p className="text-center dash-text text-wrap">Finances</p>
				</div>
			</div>
			<div className="col-6 col-md-4 ">
				{" "}
				<div
					className="dashboard"
					onClick={() => {
						Navigate(
							`students/student/books/${
								parseInt(ActiveCred.guardian) === 1
									? ActiveCred.refLinker
									: ActiveCred.linker
							}`
						);
					}}
				>
					<div className="d-flex justify-content-center">
						<Book className="dash-icon" />
					</div>
					<p className="text-center dash-text text-wrap">Books</p>
				</div>
			</div>
		</>
	);
};

export default ClientDash;
