import React from "react";
import {
	Award,
	BarChartSteps,
	BoundingBox,
	Calendar4Range,
	CashStack,
	Collection,
	ColumnsGap,
	FolderCheck,
	ListCheck,
	PersonBadge,
} from "react-bootstrap-icons";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { Dropdown } from "react-bootstrap";
const AdminDash = () => {
	const navigate = useNavigate();
	const inst = useSelector((state) => state.inst.activeInst);
	return (
		<>
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`structure/${"academic-year"}`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<Calendar4Range className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">
									Academic Years
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`structure/${"level"}`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<BarChartSteps className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">
									Levels / Classes
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			{parseInt(inst.higher) === 1 ? (
				<div className="col-6 col-md-4 ">
					<div className="dashboard mb-2">
						<Dropdown
							className="no-caret"
							style={{ width: "100%" }}
							onClick={() => {
								window.scroll(0, 0);
								navigate(`structure/${"faculty"}`);
							}}
						>
							<Dropdown.Toggle
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}
								variant="transparent"
							>
								<div>
									<div className="d-flex justify-content-center">
										<BoundingBox className="dash-icon" />
									</div>
									<p className="text-center dash-text text-wrap">Faculties</p>
								</div>
							</Dropdown.Toggle>
						</Dropdown>
					</div>
				</div>
			) : null}
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`structure/${"period"}`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<ColumnsGap className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">
									Periods / Semesters / Terms
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`subjects`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<FolderCheck className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">
									Subjects / Units
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`activities`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<ListCheck className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">
									Subjects / Units Assessments
								</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`grading`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<Award className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">Grading</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`payroll-items`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<Collection className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">Payroll Items</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>{" "}
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`staffs`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<PersonBadge className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">Staffs</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
			<div className="col-6 col-md-4 ">
				<div className="dashboard mb-2">
					<Dropdown
						className="no-caret"
						style={{ width: "100%" }}
						onClick={() => {
							window.scroll(0, 0);
							navigate(`modes`);
						}}
					>
						<Dropdown.Toggle
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}
							variant="transparent"
						>
							<div>
								<div className="d-flex justify-content-center">
									<CashStack className="dash-icon" />
								</div>
								<p className="text-center dash-text text-wrap">Payment Modes</p>
							</div>
						</Dropdown.Toggle>
					</Dropdown>
				</div>
			</div>
		</>
	);
};

export default AdminDash;
